.App {
  text-align: center;
  padding: 20px;
}

.App-header {
  background-color: #282c34;
  padding: 20px;
  color: white;
  margin-bottom: 20px;
}

main {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
}

input {
  padding: 10px;
  margin-right: 10px;
  width: 70%;
  border: 1px solid #ccc;
  border-radius: 4px;
}

button {
  padding: 10px 20px;
  background-color: #61dafb;
  border: none;
  border-radius: 4px;
  color: white;
  cursor: pointer;
}

button:hover {
  background-color: #21a1c9;
}

p {
  text-align: left;
  margin: 10px 0;
  padding: 10px;
  border-radius: 4px;
}

p strong {
  color: #61dafb;
}